import React, {useEffect, useState} from 'react';
var FontAwesome = require('react-fontawesome');

const Resizer = () => {

    const [windowWidth, setWindowWidth] = useState(window.innerWidth)
          // <img src={("https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcREgdfkC7bgqtUMu_q9uz4KyCydIecZ1ibHTjJj61JzFcx2Zwgh&usqp=CAU")} alt="" className="img-responsive" />

    function DesktopView()
{
    return(
        <div>
        <h2 className="text text-muted m-3 p-3">You are currently using desktop</h2>
         <FontAwesome
        className='super-crazy-colors'
        name='fa fa-desktop'
        size='5x'
        spin
        style={{ textShadow: '0 3px 0 rgba(0, 0, 255, 255)' }}
      />
        </div>
    )
}
function TabletView()
{
    return(
    <div>
          <h2 className="text text-muted m-3 p-3">You are currently using Tablet</h2>
          <FontAwesome
        className='super-crazy-colors'
        name='fa fa-tablet'
        size='5x'
        spin
        style={{ textShadow: '0 1px 0 rgba(0, 0, 0, 0.1)' }}
      />
        </div>
      
   
    )
}
function MobileView()
{
    return(
       <div>
        <h2 className="text text-muted m-3 p-3" >Mobile view</h2>
        <FontAwesome
        className='super-crazy-colors text-primary'
        name='fa fa-mobile'
        size='5x'
    />

          </div>
    )
}


 let componentToDisplay;

   useEffect(() => {
        
        function captureWindowResizeEvent()
        {
            const currentWidth = window.innerWidth;
            setWindowWidth(currentWidth);
        }
         window.addEventListener("resize", captureWindowResizeEvent)
         return () => {  window.removeEventListener("resize", captureWindowResizeEvent) }
},[])

        if(windowWidth>1024){
            componentToDisplay=  <DesktopView/>;
        }
        else if(windowWidth>767){
            componentToDisplay=  <TabletView/>;
        }else{
            componentToDisplay=  <MobileView/>;
        }

 return (
        <div className="bg-info container p-5">
          <h1 className="text-warning m-3 p-4"> current width of screen: <strong>{windowWidth}</strong></h1>
           {componentToDisplay}
        </div>
    );
};

export default Resizer;
