import React, { useState } from "react";
import logo from "./logo.svg";
import "./App.css";
//import UseEffectExample from './Components/useEffect';
import Resizer from "./Components/Resizer";
function App() {
  const [isVisibleMyComponent, setVisibility] = useState(true);

  return (
    <div className="App">
      <button
        className="btn shadow-lg p-3 text-info m-5 bg-dark "
        onClick={() => setVisibility(!isVisibleMyComponent)}
      >
        Toggle Visibility{" "}
      </button>
      {isVisibleMyComponent && <Resizer />}
    </div>
  );
}

export default App;
